.cards {
      padding: 2rem;
      background-color: #fff;

      display: inline-block;
}

h1 {
      text-align: center;
}

.cards__container {
      display: flex;
      flex-flow: column;
      align-items: center;
      /* max-width: 1120px; */
      width: 100%;
      margin: 10px;
      justify-content: center;
}
.cards__wrapper {
      position: relative;
      margin: 50px 0 45px;
      width: 50%;
      height: 50%;
}

.cards__items {
      margin-bottom: 24px;
      margin: 15px;
}

.cards__item {
      display: flex;
      flex: 1;
      margin: 0 1rem;
      border-radius: 20px;
}

.cards__item__link {
      display: flex;
      flex-flow: column;
      width: 100%;
      box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
      -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
      filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
      border-radius: 10px;
      overflow: hidden;
      text-decoration: none;
}

.cards__item__pic-wrapper {
      position: relative;
      width: 100%;
      padding-top: 67%;
      overflow: hidden;
      margin-right: 500px;
      align-items: center;
}

.fade-img {
      animation-name: fade-img;
      animation-duration: 2s;
}

.cards__item__pic-wrap::after {
      content: attr(data-category);
      position: absolute;
      bottom: 0;
      margin-left: 10px;
      padding: 6px 8px;
      max-width: calc((100%) - 60px);
      font-size: 12px;
      font-weight: 700;
      color: #fff;
      background-color: #1f98f4;
      box-sizing: border-box;
}

.cards__item__img {
      position: relative;
      top: 0;
      right: 10;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      max-width: 100%;
      height: 100%;
      max-height: 100%;
      object-fit: cover;
      transition: all 0.2s linear;
}

.cards__item__img:hover {
      transform: scale(1.1);
}

.cards__item__info {
      padding: 20px 30px 30px;
}

.cards__item__text {
      color: #252e48;
      font-size: 28px;
      line-height: 24px;
}

@media only screen and (min-width: 1200px) {
      .content__blog__container {
            width: 84%;
      }
}

@media only screen and (min-width: 1024px) {
      .cards__items {
            display: flex;
      }
}

@media only screen and (max-width: 1024px) {
      .cards__item {
            margin-bottom: 2rem;
      }
}
@media screen and (max-width: 500px) {
      .cards__item__text {
            color: #252e48;
            font-size: 13px;
            line-height: 24px;
      }
}
