:root {
      --primary: "#fff";
}
.btn {
      padding: 8px 20px;
      border-radius: 6px;
      outline: none;
      border: none;
      cursor: pointer;
}
.btn--primary {
      background-color: rgb(25, 208, 214);
      color: #242424;
      border: 1px solid var(--primary);
}
.btn--outline {
      background-color: rgb(23, 213, 219);
      color: #fff;
      padding: 8px 20px;
      border: 1px solid var(--primary);
      transition: all 0.3s ease-out;
}
.btn--medium {
      padding: 8px 20px;
      font-size: 20px;
}
.btn--large {
      padding: 12px 26px;
      font-size: 20px;
}
.btn--medium:hover,
.btn--large:hover {
      background: #fff;
      color: #242424;
      transition: all 0.3s ease-out;
}
